import React from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'
import { Link } from 'gatsby'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

import { desktopVw } from '../styles/utils'
import { getLinkPath, imageSources } from '../utils'

import ProductThumbnail from './ProductThumbnail'
import Carousel from './common/Carousel'
import useDetectDevices from '../hooks/useDetectDevices'

const StyledBestSellersSection = styled.section`
  margin: 20px 0 30.5px 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(33)} 0 ${desktopVw(76.6)};
  }
`

const TitleSection = styled.div`
  display: flex;
  align-items: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(44)};
  }
`

const Dash = styled.div`
  border-top: 1px solid #c4c4c4;
  width: 100%;
`

const Title = styled.p`
  font-weight: 500;
  width: 100%;
  text-align: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(22)};
    text-transform: uppercase;
  }
`

const StyledCarousel = styled(Carousel)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(-25)};
  }
`

const settings = {
  slidesToShow: 3,
  slidesToScroll: 3,
  leftArrowStyle: { left: '-32px' },
  rightArrowStyle: { right: '-5px' },
}

const StyledImage = styled(Image)`
  display: var(--displayMobile);

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const TextWrapper = styled.div`
  h1,
  h2,
  h3,
  p {
    font-size: 30px;
    line-height: 30px;
    white-space: pre-wrap;
    text-transform: uppercase;
    text-align: center;
  }

  i {
    font-family: ${props => props.theme.fonts.secondary};
    font-style: normal;
    font-weight: 600;
    font-size: 35px;
  }

  z-index: 1;
  margin: 0 50px 26px;
`

const BestSellers = styled.ul`
  display: flex;
  flex: 1;
  margin: 10px 0 0;
  padding: 0 10px;
  overflow-x: scroll;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;

  &::-webkit-scrollbar {
    display: none;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: column;
    flex-flow: wrap;
    align-items: flex-start;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }
`

const Wrapper = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(301)} !important;
    height: ${desktopVw(370)} !important;
    .product-thumbnail__image-wrapper {
      width: ${desktopVw(301)} !important;
      height: ${desktopVw(370)} !important;
    }
  }
`

const BestSellerItem = styled.li`
  padding-right: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(50.58)};
    padding: 0;
    width: calc(50% - ${desktopVw(20)});

    &:nth-last-child(-n + 2) {
      margin-bottom: 0;
    }
  }
`

const BestSellersSection = ({ json, products, tracker, className }) => {
  const { devices } = useDetectDevices()

  return (
    <StyledBestSellersSection className={className}>
      {devices.desktop && (
        <>
          <TitleSection>
            <Dash />
            <Title>You May Also Like</Title>
            <Dash />
          </TitleSection>

          <StyledCarousel {...settings}>
            {products.map((product, index) => (
              <Wrapper>
                <ProductThumbnail
                  contentfulProduct={product}
                  tracker={
                    tracker
                      ? `${tracker}_you_may_also_like__${index}`
                      : undefined
                  }
                  prefixName='You May Also Like'
                />
              </Wrapper>
            ))}
          </StyledCarousel>
        </>
      )}

      {devices.mobile && (
        <>
          <TextWrapper>{documentToReactComponents(json)}</TextWrapper>
          <BestSellers>
            {products.map((product, index) => (
              <BestSellerItem key={product.id}>
                <ProductThumbnail
                  contentfulProduct={product}
                  tracker={
                    tracker
                      ? `${tracker}_you_may_also_like__${index}`
                      : undefined
                  }
                  prefixName='You May Also Like'
                />
              </BestSellerItem>
            ))}
          </BestSellers>
        </>
      )}
    </StyledBestSellersSection>
  )
}

export default BestSellersSection
