import React from 'react'
import styled from 'styled-components'

import { colors } from '../../../styles/theme'
import {
  DetailValue,
  Edit,
  ProductOption,
  ReturnItem,
  StyledCheckbox,
  StyledLineItemThumbnail,
} from '../commons'
import {
  formatReasonCode,
  formatSubReason,
  getReturnLineItem,
} from '../../../utils/returns'
import { BLOCK_RETURNS_FOR_PROMO_CODE } from '../../../utils'
import { mobileVw } from '../../../styles/utils'

const Wrapper = styled.div`
  display: flex;
  align-items: baseline;
  margin: 0 0 -30px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
  }
`

const ErrorTextMobile = styled.div`
  color: #db3333;
  font-size: ${mobileVw(10)};
  line-height: ${mobileVw(12.5)};
  display: block;
  margin-top: ${mobileVw(10)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

export function StageZero({
  lineItems,
  activeLineItemsChecked,
  handleCheckboxClick,
  returnLineItems,
  setActiveLineItem,
  setActiveLineItemIndex,
  setModalOpen,
}) {
  return (
    <>
      {lineItems.map((lineItem, i) => {
        const returnLineItem = getReturnLineItem(returnLineItems, lineItem)
        const blockedReturnsForDiscountCode =
          (lineItem?.discountAllocations || []).filter(it =>
            BLOCK_RETURNS_FOR_PROMO_CODE.some(
              blockedPromoCode => blockedPromoCode === it?.code,
            ),
          ).length > 0
        return (
          <ReturnItem key={lineItem.id}>
            <ProductOption>
              <StyledCheckbox
                name='returnsSelection'
                value={lineItem.id}
                checked={activeLineItemsChecked.includes(i)}
                checkedColor={
                  blockedReturnsForDiscountCode ? '#C4C4C4' : colors.primary
                }
                disabled={blockedReturnsForDiscountCode}
                onClick={() => handleCheckboxClick(i)}
                showCheckmark={!blockedReturnsForDiscountCode}
              />
              <div>
                <StyledLineItemThumbnail
                  lineItem={lineItem}
                  errText={
                    blockedReturnsForDiscountCode &&
                    'Final sale. This product is not returnable'
                  }
                />
                {blockedReturnsForDiscountCode && (
                  <ErrorTextMobile>
                    Final sale. This product is not returnable
                  </ErrorTextMobile>
                )}
              </div>
            </ProductOption>
            {returnLineItem?.reasonCode && (
              <Wrapper>
                <DetailValue>
                  Return Reason:{' '}
                  {`${formatReasonCode(
                    returnLineItem?.reasonCode || '',
                  )}${formatSubReason(returnLineItem?.returnLineItemReview)}`}
                </DetailValue>
                <Edit
                  onClick={() => {
                    setActiveLineItemIndex(i)
                    setActiveLineItem(lineItem)
                    setModalOpen(true)
                  }}
                >
                  Edit
                </Edit>
              </Wrapper>
            )}
          </ReturnItem>
        )
      })}
    </>
  )
}
